.header {
    
    transition: all 0.2s ease-in-out;

	position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: 0.8rem 0;
    z-index: 401;
    


    &-right{
        .fas{
            display: none;
        }
       
    }
    &-left{
       
    }
    &__num {
        font-size: var(--fz-l);
        font-weight: 700;
        animation-duration: 1s;
            animation-name: show;
            animation-delay: 0 !important;
            &.hidden{
                animation-duration: 1s;
                animation-name: hidden;
                animation-delay: 0 !important;
            }
        &-icon {
            animation-duration: 1s;
            animation-name: show;
            animation-delay: 0 !important;
            &.hidden{
                animation-duration: 1s;
                animation-name: hidden;
                animation-delay: 0 !important;
                &.dis-none{
                    display: none;
                }
            }
        }
        &.dis-none{
            display: none;
        }
        
    }

    &-container {
        margin: 0 auto;
        
    }
    &-anchor {
        display: flex;
        align-items: center;

        animation-duration: 1s;
        animation-name: show;
        animation-delay: 0 !important;
        &.hidden{
            animation-duration: 1s;
            animation-name: hidden;
            animation-delay: 0 !important;
            animation-fill-mode: forwards;
        }
        &.dis-none{
            display: none;
        }
        .header-logo{
            &__img{
                padding-right: 25px;
                

                
                path{
                    transition: all 0.5s ease-in-out;
                }
                
            }
            &__title {
                // border-left: 1px solid #9db7d3;
                margin: 0;
                padding-left: 35px;
                position: relative;
                text-transform: uppercase;
                color: #fff;
                font-size: var(--fz-xs);
                font-weight: 700;
                height: 42px;
                border-left: 1px solid #9db7d3;

                // &::before{
                //     position: absolute;
                //     content: "";
                //     top: 50%;
                //     left: 0px;
                //     border: 1px solid #9db7d3;
                //     height: 42px;
                //     transform: translateY(-50%);
                // }
            }
        }
    }
    &-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .nav {

        &__list {
            display: flex;
            column-gap: 1.25rem;
        }

        &__link {
            font-size: var(--fz-s);
            color: var(--c-white);
        }
    }

    .header__number {
        font-size: var(--fz-s);
        color: var(--c-white);
        margin: 0 1.25rem;
        line-height: 1;
    }

    .burger {
        svg {
            width: 24px;

            path {
                transition: fill .2s linear;
            }
        }

        &:hover path {
            fill: var(--c-primary);
        }
    }

    &__logo {
        transform: translateY(-1rem);
    }
    &.fixed{
        transition: all 0.2s ease-in-out;
        background: #fff;
        .header {
            padding: 5px 0;
            &-container{
                height: 40px;
            }
            
        }
        .menu {
            path{
                fill: var(--c-secondary);
            }
        }

        .header-right{
            a{
                color:var(--c-secondary) !important;
            }
            
        }
        .aside-btn{
            height: 35px !important;
        }
        .bla{
            
            .line{
                background-color: var(--c-secondary);
            }
        }
        .header__num-icon{
            color: var(--c-secondary);
        }
    }

}
.header.change{
    transition: all 0.5s ease-in-out;
    svg{
        path{
            fill: var(--c-secondary)
        }
    }
}
.header__num.change{
    transition: all 0.5s ease-in-out;
    color: var(--c-secondary);
    &:hover{
        color: var(--c-primary);
    }
}